import Image from "next/image";

interface Proyecto {
    titulo: string;
    descripcion: string[];
    imagen: string;
    indice: number;
}

export default function Proyecto({ titulo, descripcion, imagen, indice }: Proyecto) {
    return (
        <div className={`flex flex-col md:flex-row items-center p-6 rounded-xl shadow-md 
                        ${indice % 2 === 0 ? "bg-gray-100 md:flex-row" : "bg-white md:flex-row-reverse"}`}
        >
            <Image
                src={imagen}
                alt={`Portada de ${titulo}`}
                width={350}
                height={550}
            />
            <div className="md:ml-6 md:mr-6 text-center md:text-left">
                <h3 className="text-2xl font-semibold mb-2"> {titulo} </h3>
                {descripcion.map((linea, i) => (
                    <p key={i}> {linea} </p>
                ))}
            </div>
        </div>
    );
}
